// Block About-audit
.about-audit {
	padding: 45px 0;
	overflow: hidden;

	&__container {
		position: relative;
		@include container320();
	}
	
	&__title {
		@include title320();
	}

	&__text {
		margin-top: 50px;
		@include paragraph320();
	}

	&__order {
		width: 100%;
		text-align: center;
		margin-top: 80px;
		display: inline-block;
		padding: 20px 30px;
		text-transform: uppercase;
		font-size: 1.8em;
		font-weight: 900;
		color: $color-blue;
		border: 3px solid $color-blue;

		&:hover {
			color: #fff;
			background: $bg-gradient;
		}
	}

	@media screen and(min-width: 640px) {
		&__container {
			@include container640();
		}
	}

	@media screen and(min-width: 1024px) {
		& {
			padding: 170px 0 75px;
		}
		
		&__container {
			@include container1024();
		}

		&__order {
			width: auto;
		}
	}

	@media screen and(min-width: 1250px) {
		&__container {
			@include container1250();
		}
	}
}

// Block Tech-audit
.tech-audit {
	padding: 45px 0;

	&__container {
		@include container320();
	}

	&__title {
		@include title320();
	}

	&__text {
		margin-top: 10px;
		@include paragraph320();

