			margin-top: 50px;
		}
	}

	@media screen and(min-width: 640px) {
		&__container {
			@include container640();
		}
	}

	@media screen and(min-width: 1024px) {
		& {
			padding: 100px 0 55px;
		}

		&__container {
			@include container1024();
		}

		&__title {
			@include title();
		}
	}

	@media screen and(min-width: 1250px) {
		&__container {
			@include container1250();
		}
	}
}

// Block Metriks
.metriks {
	padding: 45px 0;

	&__container {
		@include container320();
	}

	&__title {
		@include title320();
	}

	&__items-box {
		margin-top: 35px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		flex-wrap: wrap;
	}

	&__item {
		margin-top: 15px;
		width: 100%;
		padding: 20px;
		background: $bg-gradient;
		// border: 4px solid $color-blue;
	}

	&__item-title {
		text-align: center;
		font-size: 1.6em;
		font-weight: bold;
		color: #fff;
	}

	&__item-text-box {
		display: none;
		margin-top: 30px;
		padding: 0 20px;
	}

	&__item-text {
		margin-top: 10px;
		font-size: 1.5em;
		color: #fff;
	}

	@media screen and(min-width: 640px) {
		&__container {
			@include container640();
		}
	}

	@media screen and(min-width: 1024px) {
		& {
			padding: 55px 0 90px;
		}

		&__container {
			@include container1024();
		}

		&__title {
			@include title();
		}

		&__items-box {
			margin-top: 35px;
			flex-direction: row;
		}

		&__item-title {
			height: 45px;
			font-size: 1.8em;
		}

		&__item {
			margin-top: 35px;
			padding: 30px 10px 10px;
			width: 30%;
			height: 320px;
		}

		&__item-text-box {
			display: block;
		}
	}

	@media screen and(min-width: 1250px) {
		&__container {
			@include container1250();
		}
	}
}

// Block Tarifs
.tarifs {
	padding: 45px 0;

	&__container {
		@include container320();
	}

	&__title {
		@include title320();
	}

	&__subtitle {
		@include subtitle320();
	}

	&__items-box {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}

	&__item {
		margin-top: 50px;
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		padding: 40px 20px;
		background: $bg-gradient;
		// border: 1px solid $color-grey;
	}

	&__item-title {
		text-align: center;
		font-size: 2.8em;
		color: #fff;
		text-transform: uppercase;
	}

	&__item-text-box {
		margin-top: 40px;
		flex-grow: 1;
	}

	&__item-text {
		font-size: 1.8em;
		margin-top: 10px;
		color: #fff;
	}

	&__item-price-box {
		margin-top: 40px;
		padding-top: 25px;
		border-top: 3px solid #fff;
	}
	
	&__item-price-text-box {
		margin-top: 5px;
		position: relative;
	}

	&__item-price-text {
		margin-left: 30px;
		font-size: 2em;
		color: #fff;
	}

	@media screen and(min-width: 640px) {
		&__container {
			@include container640();
		}

		&__items-box {
			flex-direction: row;
		}

		&__item {
			width: 49%;
		}
	}

	@media screen and(min-width: 1024px) {
		& {
			padding: 60px 0 45px;
		}

		&__container {
			@include container1024();
		}

		&__title {
			@include title();
		}

		&__subtitle {
			@include subtitle();
		}

		&__item {
			padding: 45px 30px;
			width: 47%;
		}

		&__item-title {
			font-size: 4em;
		}

		&__item-text {
			font-size: 2.4em;
		}

		&__item-price-text {
			font-size: 3.2em;
			margin-left: 60px;
		}
	}

	@media screen and(min-width: 1250px) {
		&__container {
			@include container1250();
		}

		&__item-price-text {
			font-size: 3.8em;
			margin-left: 60px;
		}
	}
}

// Block Estimate
.estimate {
	padding: 45px 0 100px;

	&__container {
		@include container320();
	}

	&__title {
		@include title320();
	}

	&__subtitle {
		@include subtitle320();
	}

	&__form {
		margin-top: 50px;
		position: relative;
		padding: 0 25px 80px;
		display: flex;
		flex-direction: column;
		background: $bg-gradient;
	}

	&__form-planet {
		position: absolute;
		top: -85px;
    	left: -165px;
	}

	&__form-input {
		margin-top: 50px;
		font-size: 1.8em;
		font-style: italic;
		color: #fff;
		background: transparent;
		border: none;
		border-bottom: 2px solid #fff;
		text-indent: 10px;
		padding-bottom: 10px;
		border-radius: 0;
	}

	&__form-comment {
		margin-top: 65px;
		padding: 15px;
		font-size: 1.8em;
		color: $color-grey;
		font-style: italic;
		background: #fff;
		opacity: 0.6;
		border: none;
		border-radius: 0;
	}
	
	&__form-checkbox-box {
		margin-top: 50px;
		position: relative;
		text-align: center;
	}

	&__form-checkbox-pi {
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0;

		& + label {
		  	position: relative;
		  	cursor: pointer;

		  	&:before {
		  		content: '';
			  	position: absolute;
			  	top: -4px;
			  	left: 0;
			  	width: 18px;
			  	height: 18px;
			  	background: #fff;
			  	border: 1px solid $color-grey;
			  	transition: .2s;
		  	}

		  	&:after {
		  		content: '';
				position: absolute;
				top: 0;
				left: 4px;
				width: 12px;
				height: 12px;
				background: $color-blue;
				opacity: 0;
				transition: all 150ms ease;
		  	}
		}

		&:checked + label:after {
			opacity: 1;
		}
	}

	&__form-checkbox-pi-text {
		margin-left: 25px;
		font-size: 1.4em;
		color: #fff;

		&--link {
			display: block;
			font-weight: bold;
			color: #fff;
		}
	}

	&__form-submit {
		width: 100%;
		margin: 50px auto 0;
		padding: 20px;
		border: 4px solid #fff;
		background: transparent;
		color: #fff;
		font-size: 2em;
		text-transform: uppercase;
		cursor: pointer;
		border-radius: 0;
		transition: all 200ms ease;

		&:hover {
			background: #fff;
			color: $color-blue;
			border: 4px solid $color-blue;
		}
	}

	@media screen and(min-width: 640px) {
		&__container {
			@include container640();
		}

		&__form {
			padding: 0 60px 80px;
		}

		&__form-input {
			text-indent: 20px;
		}

		&__form-comment {
			text-indent: 20px;
		}
	}

	@media screen and(min-width: 1024px) {
		& {
			padding: 45px 0 120px;
		}

		&__container {
			@include container1024();
		}

		&__title {
			@include title();
		}

		&__subtitle {
			@include subtitle();
		}

		&__form {
			margin-top: 90px;
			padding: 0 20% 80px;
		}

		&__form-input {
			text-ident: 30px;
			font-size: 2em;
		}

		&__form-comment {
			padding: 15px 35px;
			font-size: 2em;
		}

		&__form-submit {
			width: auto;
			padding: 20px 90px;
		}

		&__form-checkbox-pi-text {
			font-size: 2em;
		}
	}

	@media screen and(min-width: 1250px) {
		&__container {
			@include container1250();
		}
	}
}



